	module.exports = {"data":{"wordpressPage":{"title":"Informatie","content":"<header class=\"post-header\"></header>\n<section class=\"post-content\">\n<blockquote><p>Comfort Food is meer dan alleen lekker eten. Het gaat om verbondenheid met familie, vrienden en gelijkgestemden in een relaxte sfeer met goede muziek.</p></blockquote>\n<p>Eten, muziek en mensen. Pass the Peas mobiele catering brengt deze samen. De geur van vers gebakken kip, met op de achtergrond soul, jazz, funk of hip hop. Of je nu gemoedelijk in een schommelstoel heen en weer deint, languit op kussens ligt of aan een picknicktafel zit met je vrienden: bij Pass the Peas kan je even echt relaxen, tijdens een feest, festival of food market.</p>\n<h1 id=\"wieistonny\">Wie is Tonny?</h1>\n<p>Tonny Wilbers is de chef van en de ziel achter Pass the Peas. Geboren in Gennep-Limburg, maar qua kookstijl gevormd op Curaçao en in Amsterdam. Vanaf mei 2013 is hij van start gegaan met zijn<br />\neigen mobiele keuken en kookt hij alleen nog maar op de manier zoals hij zelf wil: op locatie. Met zijn rollende keuken komt hij overal. Tonny over zijn eten: Comfort food zoals comfort food bedoeld is. Tonny wordt bijgestaan door een vaste kern van ervaren horecamensen die met evenveel passie en plezier werken om het gasten naar de zin te maken.</p>\n<h1 id=\"watwedoen\">Wat we doen</h1>\n<p>Als mobiele cateraar spelen we flexibel in op de vraag van onze klant. Met onze rollende keuken<br />\nkomen we overal. Van festival tot diner in de privé sfeer, we zijn van alle markten thuis. We zijn<br />\nbijvoorbeeld ook in te huren voor bedrijfsfeesten &amp; bruiloften.</p>\n</section>\n","slug":"informatie","id":"136e1e73-a255-5fbc-810e-90a66b0d9895","date":"February 06, 2018"},"site":{"id":"Site","siteMetadata":{"title":"Pass The Peas"}}},"pathContext":{"id":"136e1e73-a255-5fbc-810e-90a66b0d9895"}}

